import './additem.css';
import { useState } from 'react';
import { unmountComponentAtNode } from 'react-dom';

function AddItem() {
    const [showState, setShowState] = useState("");
    const [serviceState, setServiceState] = useState("");
    const [categoryState, setCategoryState] = useState("Drama 1");

    function close() {
        document.getElementById('overlay').style.display = "none";
        unmountComponentAtNode(document.getElementById('overlay'));
    }

    function handleSubmit() {
        if (showState.length > 0 && serviceState.length > 0) {
            fetch('https://jr2xqlk4g4.execute-api.us-east-1.amazonaws.com/dev/create', {
                "method": "POST",
                "headers": {
                    "Content-Type": "application/json",
                    "Access-Control-Allow-Origin": "*"
                },
                "body": JSON.stringify({ showname: showState, service: serviceState, category: categoryState.replaceAll(" ", "_") })
            }).then(() => {
                close();
                window.location.reload();
            });
        } else {
            alert("Either the show name or the service name is not entered");
        }
    }

    return (
        <div className='wrapper'>
            <div className='close-button-wrapper'>
                <i className='bi bi-x' onClick={() => close()}></i>
            </div>
            <div className='form-wrapper'>
                <label>
                    <p>Show Name</p>
                    <input value={showState} onChange={(event) => setShowState(event.target.value)}/>
                </label>

                <label>
                    <p>Service</p>
                    <input value={serviceState} onChange={(event) => setServiceState(event.target.value)}/>
                </label>

                <label class='category-label'>
                    <p>Category</p>
                    <div class='select'>
                        <select onChange={(event) => setCategoryState(event.target.value)}>
                            <option>Drama 1</option>
                            <option>Drama 2</option>
                            <option>Comedy</option>
                        </select>
                    </div>
                </label>

                <button onClick={() => handleSubmit()}>Submit</button>
            </div>
        </div>
    );
}

export default AddItem;