import { Component } from 'react';
import { render } from 'react-dom';

import SeeAll from '../seeall/SeeAll';
import AddItem from '../additem/AddItem';
import ShowTile from './ShowTile/ShowTile';
import SelectCategory from '../selectCategory/SelectCategory';

class Home extends Component {
    constructor(props) {
        super(props);

        this.state = { shows: [] };

        this.getShows = this.getShows.bind(this);
        this.setupSeeAll = this.setupSeeAll.bind(this);
        this.setupAddItem = this.setupAddItem.bind(this);
    }

    getShows() {
        fetch(`https://jr2xqlk4g4.execute-api.us-east-1.amazonaws.com/dev/shows?category=${localStorage.getItem('category')}`)
            .then(blob => blob.json())
            .then(data => this.setState({ shows: data }))
    }

    setupAddItem() {
        document.getElementById('overlay').style.display = "flex";
        render(<AddItem/>, document.getElementById('overlay'));
    }

    setupSeeAll() {
        document.getElementById('overlay').style.display = "flex";
        render(<SeeAll/>, document.getElementById('overlay'));
    }

    componentDidMount() {
        this.getShows();
    }
    
    render() {
        if (this.state.shows.length > 0) {
            return (
                <div className='App'>
                    <SelectCategory/>
                    {
                        this.state.shows.map(show => {
                            return (
                                <ShowTile key={show._id} show={show.show} service={show.service}/>
                            );
                        })
                    }
                    <div className='button-wrapper'>
                        <button onClick={() => this.setupAddItem()}><i className="bi bi-plus"></i>Add New Shows</button>
                        <button onClick={() => this.getShows()}><i className="bi bi-arrow-repeat"></i> Refresh Choices</button>
                        <button onClick={() => this.setupSeeAll()}><i className="bi bi-three-dots-vertical"></i>See All</button> 
                    </div>
                </div>
            );
        } else {
            return (
                <div className='App'>
                    <SelectCategory/>
                    <div className='no-content-wrapper'>
                        <h1>No Shows Yet</h1>
                    </div>
                    <div className='button-wrapper'>
                        <button onClick={() => this.setupAddItem()}><i className="bi bi-plus"></i>Add Your First Show</button>
                    </div>
                </div>
            );
        }
    }
}

export default Home;