import './seeall.css';
import { useState, useEffect } from 'react';
import { unmountComponentAtNode } from 'react-dom';

import SeeAllItem from './SeeAllItem/SeeAllItem';

function SeeAll() {
    const [showsState, setShowsState] = useState([]);

    function close() {
        document.getElementById('overlay').style.display = "none";
        unmountComponentAtNode(document.getElementById('overlay'));
    }

    useEffect(() => {
        fetch('https://jr2xqlk4g4.execute-api.us-east-1.amazonaws.com/dev/all')
            .then(blob => blob.json())
            .then(data => setShowsState(data));
    }, []);

    return (
        <div className='wrapper'>
            <div className='close-button-wrapper'>
                <i className='bi bi-x' onClick={() => close()}></i>
            </div>
            <div className='content-wrapper'>{
                showsState.map((show, i) => {
                    return <SeeAllItem 
                        key={show._id} 
                        _id={show._id}
                        id={show._id}

                        close={close}

                        number={i}
                        show={show.show}
                        service={show.service}
                    />
                })
            }</div>
        </div>
    );
}

export default SeeAll;