import './selectcategory.css';
import { render } from 'react-dom';
import { useState } from 'react';
import Selector from './Selector/Selector';

function SelectCategory() {
    const [categoryState, setCategoryState] = useState(localStorage.getItem('category'));

    function getCategory() {
        return categoryState
            .split('')[0].toUpperCase()
            .concat(categoryState
                .slice(1, categoryState.length)
            )
            .replaceAll("_", " ");
    }

    function updateCategory(value) {
        localStorage.setItem('category', value.replaceAll(" ", "_"));
        setCategoryState(value);
    }

    function setSelectorOverlay() {
        document.getElementById('overlay').style.display = "flex";
        render(<Selector updateCategory={updateCategory}/>, document.getElementById('overlay'));
    }

    return (
        <div className='select-category'>
            <p onClick={() => setSelectorOverlay()} className='category-select-text'>Category: { getCategory() }</p>
        </div>
    )
}

export default SelectCategory;