import './seeallitem.css';

function SeeAllItem(props) {
    function deleteItem(id) {
        fetch('https://jr2xqlk4g4.execute-api.us-east-1.amazonaws.com/dev/delete', {
            "method": "POST",
            "headers": {
                "Content-Type": "application/json",
                "Access-Control-Allow-Origin": "*"
            },
            "body": JSON.stringify({ id: id })
        }).then(() => {
            window.location.reload();
        });
    }

    return (
        <div className='show-seeall-item' style={{ backgroundColor: props.number % 2 === 0 ? "#ccc" : "#fff" }} title="Delete this show">
            <p>{props.show}</p>
            <p>{props.service}</p>
            <i className='bi bi-x' onClick={() => deleteItem(props._id)}></i>
        </div>
    );
}

export default SeeAllItem;