import './selector.css';
import { unmountComponentAtNode } from 'react-dom';

function Selector(props) {
    function close() {
        document.getElementById('overlay').style.display = "none";
        unmountComponentAtNode(document.getElementById('overlay'));
        window.location.reload();
    }

    function updateCategorySelection(event) {
        if (event.target.value !== "Any") props.updateCategory(event.target.value);
        else props.updateCategory(event.target.value.toLowerCase());
    }

    return (
        <div className='wrapper'>
            <div className='close-button-wrapper'>
                <i className='bi bi-x' onClick={() => close()}></i>
            </div>
            
            <div className='select-wrapper'>
                <p>Select Your Category</p>
                <div className="select">
                    <select onChange={(event) => updateCategorySelection(event)}>
                        <option>Any</option>
                        <option>Drama 1</option>
                        <option>Drama 2</option>
                        <option>Comedy</option>
                    </select>
                </div>
                <button onClick={() => close()}>Update</button>
            </div>
        </div>
    );
}

export default Selector;