import './showtile.css';

function ShowTile(props) {
    function getLetters(name) {
        let words = name.split(' ');
        return words.map(word => word[0].toUpperCase()).join('');
    }
    
    return (
        <div className='showtile'>
            <div className='letters'>
                <p>{getLetters(props.show.trim())}</p>
            </div>
            <div className='info'>
                <p className='show-title'>{props.show}</p>
                <p className='show-service'>{props.service}</p>
            </div>
        </div>
    );
}

export default ShowTile;